@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
.button-toggle {
  display: block;
  cursor: pointer;
  padding: 16px;
  border: none;
  background: none;
}
.button-toggle::after {
  content: "";
  display: block;
  width: 23px;
  height: 24px;
  background: url("../img/burger-button.svg") center no-repeat;
}
.button-toggle:hover {
  opacity: 0.6;
}
.button-toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .button-toggle {
    display: none;
  }
}
.button-toggle--close {
  position: absolute;
  z-index: 3;
}
.button-toggle--close::after {
  content: "";
  display: block;
  width: 23px;
  height: 24px;
  background: url("../img/icon-cross.svg") center no-repeat;
}
@media (min-width: 768px) {
  .button-toggle--close {
    display: none;
  }
}
.button {
  display: block;
  margin: 0 auto;
  border: none;
  cursor: pointer;
  text-transform: uppercase;
  font-family: inherit;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.44;
  background: #81b3d2;
  color: #ffffff;
}
.button:hover {
  background: #669ec0;
}
.button:active {
  background: #5496bd;
}
.button:disabled {
  background: #e5e5e5;
}
.card {
  width: 100%;
  padding-top: 25px;
  border-top: 1px solid #e5e5e5;
  position: relative;
}
@media (min-width: 768px) {
  .card {
    width: 50%;
    border: 0;
    padding: 0;
  }
}
@media (min-width: 1200px) {
  .card {
    width: 33.33%;
  }
}
.card__block {
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}
@media (min-width: 768px) {
  .card__block {
    align-items: flex-start;
    width: 100%;
  }
}
.card__link {
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .card__link-position {
    width: 100%;
  }
}
.card__img {
  display: block;
  width: 100%;
  height: auto;
}
.card__name {
  margin: 20px 0;
  width: 100%;
  display: grid;
  grid-template-columns: 40px 40px 100px auto;
  grid-template-rows: auto auto auto;
}
@media (min-width: 768px) {
  .card__name {
    padding: 0 20px;
    grid-template-columns: 80px 100px auto 30px;
    grid-template-rows: auto auto;
    align-items: center;
  }
}
@media (min-width: 1200px) {
  .card__name {
    content: "";
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 3;
    margin: 0;
    padding: 20px;
    background: rgba(0, 0, 0, 0.3);
  }
}
.card__title {
  grid-column: 1/-1;
  display: block;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.5;
  color: #000000;
}
@media (min-width: 768px) {
  .card__title {
    grid-column: 1/3;
  }
}
@media (min-width: 1200px) {
  .card__title {
    color: #ffffff;
  }
}
.card__text {
  margin: 0;
  grid-column: 1/3;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.5;
  color: #333333;
}
@media (min-width: 768px) {
  .card__text {
    grid-column: 1/2;
  }
}
@media (min-width: 1200px) {
  .card__text {
    color: #ffffff;
  }
}
.card__author {
  margin: 0;
  grid-column: 3/4;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.5;
  color: #333333;
}
@media (min-width: 768px) {
  .card__author {
    grid-column: 2/3;
  }
}
@media (min-width: 1200px) {
  .card__author {
    color: #ffffff;
  }
}
.card__button {
  grid-column: 1/2;
  grid-row: 3/4;
  display: block;
  padding: 0;
  width: 23px;
  height: 21px;
  cursor: pointer;
  background: none;
  border: 0;
}
@media (min-width: 768px) {
  .card__button {
    margin-right: 10px;
    grid-column: 3/4;
    grid-row: 1/-1;
    justify-self: end;
  }
}
.card__icon {
  display: block;
  width: 23px;
  height: 21px;
  color: rgba(0, 0, 0, 0.3);
  fill: currentColor;
}
@media (min-width: 1200px) {
  .card__icon {
    color: #ffffff;
    fill: currentColor;
  }
}
.card__counter {
  margin-top: 5px;
  display: block;
  grid-column: 2/3;
  grid-row: 3/4;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.5;
  color: #000000;
}
@media (min-width: 768px) {
  .card__counter {
    grid-column: 4/5;
    grid-row: 1/-1;
  }
}
@media (min-width: 1200px) {
  .card__counter {
    color: #ffffff;
  }
}
.catalog {
