.fieldset {
  width: 100%;
  padding: 25px 0 40px 0;
  margin: 0;
  border: 0;
  border-top: 1px solid #e5e5e5;
  outline: none;
  outline-offset: 0;
  text-align: center;
  text-transform: uppercase;
  position: relative;
  font-weight: 700;
}
@media (min-width: 768px) {
  .fieldset {
    width: 50%;
    border-top: 0;
    padding: 0;
  }
  .fieldset:first-child {
    border-right: 1px solid #dbdbdb;
    padding-right: 43px;
  }
  .fieldset:nth-child(2) {
    padding-left: 43px;
  }
  .fieldset:nth-child(n + 3) {
    width: 100%;
    margin-top: 48px;
  }
}
.fieldset__list {
  width: 100%;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .fieldset__list--position {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
  }
}
@media (min-width: 1200px) {
  .fieldset__list--position {
    padding: 0 45px;
    flex-wrap: nowrap;
    justify-content: center;
  }
}
.fieldset__title {
  width: 100%;
  margin: 0 auto 28px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.44;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .fieldset__title--decoration {
    display: block;
    position: relative;
    width: 310px;
  }
  .fieldset__title--decoration::before {
    content: "";
    display: block;
    width: 123px;
    height: 1px;
    position: absolute;
    bottom: 50%;
    left: -60%;
    z-index: 1;
    background: #dbdbdb;
  }
  .fieldset__title--decoration::after {
    content: "";
    display: block;
    width: 123px;
    height: 1px;
    position: absolute;
    bottom: 50%;
    right: -60%;
    z-index: 1;
    background: #dbdbdb;
  }
}
@media (min-width: 768px) and (min-width: 1200px) {
  .fieldset__title--decoration::before {
    width: 208px;
    left: -100%;
  }
}
@media (min-width: 768px) and (min-width: 1200px) {
  .fieldset__title--decoration::after {
    width: 208px;
    right: -100%;
  }
}
.fieldset__item {
  width: 100%;
  padding: 20px 0;
  display: grid;
  grid-template-columns: auto 58px;
  grid-template-rows: auto auto;
}
.fieldset__label {
  grid-column: 1/-1;
  justify-self: start;
  font-family: inherit;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.5;
  color: #333333;
  margin-left: 16px;
}
.fieldset__input {
  grid-column: 1/-1;
  font-family: inherit;
  display: block;
  width: 100%;
  margin: 0;
  border: 4px solid rgba(0, 0, 0, 0);
  padding: 12px 16px;
  outline: none;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.44;
  color: #000000;
  background: #f2f2f2;
  align-self: center;
}
.fieldset__input:hover {
  background: #e5e5e5;
}
.fieldset__input:focus {
  background: #ffffff;
  border: 4px solid #e5e5e5;
}
.fieldset__input--width {
  grid-column: 1/2;
}
.fieldset__icon {
  grid-column: 2/3;
}
.fieldset__item-impression {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
@media (min-width: 768px) {
  .fieldset__item-impression {
    width: 50%;
  }
}
@media (min-width: 1200px) {
  .fieldset__item-impression {
    width: 100%;
    justify-content: center;
  }
}
.fieldset__label-impression {
  text-transform: initial;
  margin-bottom: 16px;
}
.fieldset__radio {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.fieldset__radio + label {
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
}
.fieldset__radio + label::before {
  content: "";
  display: block;
  margin-right: 16px;
  width: 46px;
  height: 46px;
  border-radius: 100%;
  background: #f2f2f2;
  cursor: pointer;
}
.fieldset__radio:not(:disabled):not(:checked) + label:hover::before {
  background: #e2e2e2;
}
.fieldset__radio:not(:disabled):active + label::before {
  background-color: #e5e5e5;
  background-image: url("../img/radio-dot.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.fieldset__radio:checked + label::before {
  background-color: #d2d2d2;
  background-image: url("../img/radio-dot.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.fieldset__checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.fieldset__checkbox + label {
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
}
.fieldset__checkbox + label::before {
  content: "";
  display: block;
  margin-right: 16px;
  width: 46px;
  height: 46px;
  border-radius: 4px;
  background: #f2f2f2;
  cursor: pointer;
}
.fieldset__checkbox:not(:disabled):not(:checked) + label:hover::before {
  background: #e2e2e2;
}
.fieldset__checkbox:not(:disabled):active + label::before {
  background-color: #e5e5e5;
  background-image: url("../img/tick.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.fieldset__checkbox:checked + label::before {
  background-color: #d2d2d2;
  background-image: url("../img/tick.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.fieldset__textarea {
  font-family: inherit;
  display: block;
  width: 100%;
  margin: 28px auto 0;
  padding: 0;
  min-height: 160px;
  border: 0;
  outline: none;
