*,
*::before,
*::after {
  box-sizing: border-box;
}
body {
  margin: 0;
}
.page__body {
  font-family: "PT Sans", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.44;
  counter-reset: numeration;
  background: #f2f2f2;
}
.reasons {
  counter-reset: reasons__title;
}
.reasons__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}
@media (min-width: 768px) {
  .reasons__list {
    flex-direction: row;
    flex-wrap: wrap;
  }
}
.reasons__list::before {
  display: block;
  content: "";
  width: 100%;
  height: 37px;
  position: absolute;
  top: -20px;
  left: 0;
  z-index: 3;
  background: url("../img/reasons-frame.svg") center no-repeat;
  background-size: 100%;
}
@media (min-width: 768px) {
  .reasons__list::before {
    display: none;
  }
}
.reasons__sub-list {
  padding: 25px 0 52px;
}
.reasons__img {
  display: block;
  width: 100%;
}
.reasons__item {
  padding-top: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: #81b3d2;
}
@media (min-width: 768px) {
  .reasons__item {
    width: 100%;
    padding: 0;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }
}
@media (min-width: 768px) {
  .reasons__item--order {
    width: 100%;
    flex-direction: row-reverse;
    flex-wrap: nowrap;
  }
}
.reasons__item--view {
  background: #eeeeee;
  position: relative;
}
@media (min-width: 768px) {
  .reasons__item--view {
    padding: 40px 36px 60px 36px;
    width: 33.33%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
  }
}
@media (min-width: 768px) {
  .reasons__wrap {
    width: 33.33%;
    padding: 0 20px;
  }
}
.reasons__title {
  margin: 0 0 49px 0;
  text-align: center;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.44;
  color: #ffffff;
  position: relative;
}
.reasons__title::after {
  position: absolute;
  bottom: -35px;
  left: 50%;
  z-index: 1;
  transform: translateX(-50%);
